.aboutMe {
    display: flex;
    flex-direction: column;
    padding: 1em;
    color: white;
}

.aboutMe>h2>span {
    color: #ff8922;
}

.aboutMe>p {
    font-weight: 700;
    text-align: center;
    max-width: 600px;
}


.resumeButton {
    color: dimgray;
    border: 2px solid lightgray ;
    color: lightgray;
    /* background-color: #ff8922; */
    text-align: center;
    padding: .5em;
    border-radius: 10px;
    max-width: 150px;
    font-size: 1.3rem;
    font-weight: 700;
}

.resumeButton:hover,
.resumeButton:focus {
    box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
    transform: translateY(-0.25em);
    color: #e4cb58;
    border: 2px solid #e4cb58;
    box-shadow: 2px 2px 2px rgba(228, 203, 88, 0.8)
}

.container {
    background: hsl(227, 10%, 10%);
    height: auto;
    padding: 2em 0em;
    color: white;
}

.projectsForHome {
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 1em;
}



@media(min-width:600px) {
    .aboutMe{
        width: 80%;
        align-self: center;
    }
    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .aboutMe>p{
        font-size: 1.2rem;
        text-align: left;
    }
    .aboutMe>h2{
        font-size: 1.8rem;
        margin: 0;
    }
    .aboutMe>h3 {
        font-size: 1.6rem;
    }

}


@media(min-width:900px){
    .aboutMe {
        width: 80%;
    }
        .aboutMe>p {
            font-size: 1.5rem;
            max-width: 800px;
            text-align: left;
        }
}